import { Link, useLocation, useParams } from "react-router-dom";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import style from "../../../StoreManager/StoreManager.module.css";
const PayrollOneHeadCard = ({ mt, amountObj }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={`/q/${params.username}/member/staff/payroll?type=one-head&n=staff`}
      state={{
        ...getQuery.state,
        headCard: mt,
      }}
    >
      <div className={style.smc_card}>
        <div className={style.smc_card_contatiner}>
          <img
            src={`${assestsUserFinanceUrl}/category.svg`}
            alt="categor icon"
          />
          <div className={style.smc_card_contatiner_inner}>
            <h6>{mt?.name ?? ""}</h6>
            <p>
              {t("amount")} {": "} {amountObj && amountObj[mt?.value]}
            </p>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default PayrollOneHeadCard;
