import { useTranslation } from "react-i18next";
import { useLocation, useSearchParams } from "react-router-dom";
import PayrollOneHeadCard from "./PayrollOneHeadCard";
import style from "../../../FinanceManager/FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import PayrollOneHeadStaffList from "./PayrollOneHeadStaffList";
import { useCalendar } from "../../../../Hooks/CustomHooks/use-calendar";
import dayjs from "dayjs";
import SelectFinancialYear from "../../SelectFinancialYear";
import { useState } from "react";
import { usePayrollOneSalaryHead } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
const PayrollOneHead = ({ instituteId, pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { selectedDate, setSelectedDate } = useCalendar();
  const [financialYear, setFinancialYear] = useState(false);

  const { payrollOneSalaryHead } = usePayrollOneSalaryHead({
    data: {
      shid: getQuery.state?.head?._id,
    },
    skip: !getQuery.state?.head?._id,
  });
  const onYearToggle = () => {
    setFinancialYear((pre) => !pre);
  };

  const onYearChoose = (value) => {
    setSelectedDate(
      (prev) => new Date(`${prev.getMonth() + 1} ${prev.getDate()} ${value}`)
    );
    onYearToggle();
  };

  // console.info("payrollOneSalaryHead", payrollOneSalaryHead);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <h6>{getQuery.state?.head?.heads_name ?? ""}</h6>
            </div>
            <div className={style.fm_header_start_container}>
              <h6
                style={{
                  cursor: "pointer",
                }}
                onClick={onYearToggle}
              >
                {t("financial_year")} : {dayjs(selectedDate).format("YYYY")}
              </h6>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {searchParams.get("n") === "staff" ? (
            <PayrollOneHeadStaffList instituteId={instituteId} pid={pid} />
          ) : (
            monthList?.map((mt) => (
              <PayrollOneHeadCard
                key={mt?.name}
                mt={mt}
                amountObj={payrollOneSalaryHead?.obj}
              />
            ))
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {financialYear && (
        <SelectFinancialYear
          onClose={onYearToggle}
          onYearChoose={onYearChoose}
        />
      )}
    </>
  );
};

export default PayrollOneHead;

const monthList = [
  {
    name: "April",
    value: "04",
  },
  {
    name: "May",
    value: "05",
  },
  {
    name: "June",
    value: "06",
  },
  {
    name: "July",
    value: "07",
  },
  {
    name: "August",
    value: "08",
  },
  {
    name: "September",
    value: "09",
  },
  {
    name: "October",
    value: "10",
  },
  {
    name: "November",
    value: "11",
  },
  {
    name: "December",
    value: "12",
  },
  {
    name: "January",
    value: "01",
  },
  {
    name: "February",
    value: "02",
  },
  {
    name: "March",
    value: "03",
  },
];
