import { useCallback, useEffect, useState } from "react";
import Loading from "../../../../Loader/Loading";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../../FinanceManager/FinanceManager.module.css";
import PayrollOneHeadStaffListCard from "./PayrollOneHeadStaffListCard";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { usePayrollOneSalaryHeadAllStaff } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
const PayrollOneHeadStaffList = ({ instituteId, pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { payrollOneSalaryHeadAllStaff, payrollOneSalaryHeadAllStaffLoading } =
    usePayrollOneSalaryHeadAllStaff({
      data: {
        key: getQuery?.state?.head?.heads_key,
        pid: pid,
        month: getQuery.state?.headCard?.value,
        year: getQuery.state?.financialYear,
        section: getQuery?.state?.head?.heads_type,
        page: page,
        limit: 10,
        search: search,
      },
      skip: pid ? !getQuery?.state?.head?.heads_key : !pid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (payrollOneSalaryHeadAllStaff?.list?.length) {
      let obj = nextPage(payrollOneSalaryHeadAllStaff?.list?.length);
      setState(() => obj);
    }
  }, [payrollOneSalaryHeadAllStaff?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  console.info("getQuery", getQuery.state);
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.5rem",
        }}
      >
        <section
          className={style.fm_search_full_container}
          style={{
            gap: "1rem",
          }}
        >
          <div
            className={style.fm_search_full_container_inner}
            style={{
              flex: "1.5 1.5",
            }}
          >
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
      </div>
      {payrollOneSalaryHeadAllStaff?.list?.map((staff, index) =>
        payrollOneSalaryHeadAllStaff?.list?.length === index + 1 ? (
          <div key={staff?._id} ref={ref}>
            <PayrollOneHeadStaffListCard staff={staff} />
          </div>
        ) : (
          <PayrollOneHeadStaffListCard staff={staff} key={staff?._id} />
        )
      )}
      {payrollOneSalaryHeadAllStaffLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        payrollOneSalaryHeadAllStaff?.list?.length === 0 && (
          <EmptyMessage title="No staffs found related this search." />
        )
      ) : (
        payrollOneSalaryHeadAllStaff?.list?.length === 0 && (
          <EmptyMessage title="No staffs." />
        )
      )}
    </>
  );
};

export default PayrollOneHeadStaffList;
